@charset "utf-8";

.kolonsport-site {
 .report {
   margin: 78px 0 140px;
   color: #1f1f1f;
   h3 {
     color: #1f1f1f;
     font-size: 22px;
     margin-bottom: 52px;
   }
   h5 {
     font-size: 18px;
     font-weight: bold;
     margin-top: 65px;
     margin-bottom: 28px;
     padding-bottom: 16px;
     border-bottom: 3px solid #1f1f1f;
     &:first-child {
       font-size: 14px;
     }
   }
   .checkbox {
     margin-right: 12px;
   }
   dl.report-form {
     padding-top: 19px;
     font-size: 14px;
     dt {
       float: left;
       width: 130px;
       line-height: 31px;
       margin-bottom: 26px;
       .required {
         background-color: #f23d4e;
         border-radius: 50%;
         display: inline-block;
         font: 0/0 a;
         height: 4px;
         margin-left: 5px;
         vertical-align: middle;
         width: 4px;
       }
     }
     dd {
       width: 100%;
       margin-bottom: 26px;
       .col-1 {
         width: 213px;
       }
       .col-2 {
         width: 693px;
       }
       .col-3 {
         width: 821px;
       }
       input,
       select,
       button {
         height: 31px;
         line-height: 31px;
         font-size: 14px;
       }
       select {
         padding-bottom: 4px;
       }
       textarea {
         height: 130px;
         resize: none;
         font-size: 14px;
       }
       input[type="file"] {
         display: none;
       }
       .uploadBtn {
         height: 31px;
         line-height: 31px;
         font-size: 14px;
       }
       input[type="radio"] {
         margin-left: 12px;
         display: inline-block;
         position: relative;
         width: 14px;
         height: 14px;
         margin-top: -2px;
         border-radius: 0;
         border: 0;
         vertical-align: middle;
         box-sizing: border-box;
         background: #fff url('/_ui/img/kolonsport/pc/icn/icn-radio-off.png') no-repeat center;
         background-size: 14px 14px;
         appearance: none;
         &:checked {
           background: #fff url('/_ui/img/kolonsport/pc/icn/icn-radio-on.png') no-repeat center;
           background-size: 14px 14px;
         }
       }
       label {
         margin-right: 7px;
       }
     }
   }
   .agree_detail {
     background-color: #f5f5f5;
     color: #898989;
     padding: 25px 24px;
     line-height: 1.8;
     font-size: 14px;
     li {
       margin-left: 20px;
       list-style: decimal;
     }
   }
   .buttons {
     border: 0;
     padding: 0;
     margin-top: 47px;
   }
 }
}
